import { useState, useEffect } from "react";

import styles from "./lecturer.scss";
import schedule from "../../../../api/scheduleL.json";

const Lecturer = () => {
  const [department, setDepartment] = useState("");
  const [day, setDay] = useState("");
  const [search, updateSearch] = useState("");
  let scheduleDay = [];

  useEffect(() => {
    filteredSchedule;
  });

  // Фільтр даних по цикловій комісії, дню та пошуку
  let filteredSchedule = schedule.map((arr) =>
    arr.filter((schoolDay) => {
      console.log();
      let byDepartment = schoolDay.department.indexOf(department) !== -1;
      let byDay = schoolDay.day.indexOf(day) !== -1;
      let bySearch = schoolDay.lecturer.indexOf(search.toUpperCase()) !== -1;

      if (search === "") {
        return byDepartment && byDay;
      } else {
        return byDay && bySearch;
      }
    })
  );

  // Всі вхідні дані заносяться у масив. За допомогою масиву і слайсу реалізовується пагінація
  filteredSchedule.map((department) =>
    department.map((group) => scheduleDay.push(group))
  );

  return (
    <div className={styles.lecturer}>
      <div className={styles.filter}>
        <select
          className={styles.department}
          name="departament"
          value={department}
          onChange={() => setDepartment(event.target.value)}
        >
          <option value="">Оберіть циклову комісію</option>
          <option value="Природничі дисципліни">Природничі дисципліни</option>
          <option value="Фізичні дисципліни">Фізичні дисципліни</option>
          <option value="Українська та світові культури">
            Українська та світові культури
          </option>
          <option value="Соціально-гуманітарні дисципліни">
            Соціально-гуманітарні дисципліни
          </option>
          <option value="Іноземні мови">Іноземні мови</option>
          <option value="Здоров'я та фізвиховання">
            Здоров'я та фізвиховання
          </option>
          <option value="Математичні дисципліни">Математичні дисципліни</option>
          <option value="Програмна інженерія">Програмна інженерія</option>
          <option value="Системна інженерія">Системна інженерія</option>
          <option value="Інформатики та компютерної інженерії">
            Інформатики та компютерної інженерії
          </option>
          <option value="Обслуговування верстатів з ПУ і робототехнічних комплексів">
            Обслуговування верстатів з ПУ і робототехнічних комплексів
          </option>
          <option value="Електроніка та телекомунікації">
            Електроніка та телекомунікації
          </option>
          <option value="Електротеплотехнічні вимірюваня">
            Електротеплотехнічні вимірюваня
          </option>
          <option value="Економічна дисципліна">Економічна дисципліна</option>
        </select>
        <select
          id="days"
          name="days"
          value={day}
          onChange={() => setDay(event.target.value)}
        >
          <option value="">Оберіть день</option>
          <option value="понеділок">Понеділок</option>
          <option value="вівторок">Вівторок</option>
          <option value="середа">Середа</option>
          <option value="четвер">Четвер</option>
          <option value="п'ятниця">П'ятниця</option>
        </select>
        <input
          type="search"
          name="search"
          id="search"
          value={search}
          onChange={() => updateSearch(event.target.value)}
          placeholder="Введіть викладача"
        />
      </div>
      {scheduleDay.map((lessonDay, key) => (
        <Schedule day={lessonDay} key={key} selectedDay={day} />
      ))}
    </div>
  );
};

const Schedule = (props) => {
  return (
    <div className={styles.day}>
      <div className={styles.header}>
        <span className={styles.weekday}>{props.day.day}</span>
        <span>{props.day.department}</span>
      </div>
      <div className={styles.lecturer}>{props.day.lecturer}</div>
      {props.day.lessons.map((lesson) => (
        <div className={styles.lesson}>
          <div className={styles.start}>{lesson.start}</div>
          <div className={styles.groupAndAudience}>
            <span className={styles.group}>{lesson.group},</span>
            <span className={styles.group}>{lesson.subject},</span>
            <span>{lesson.audience}</span>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Lecturer;
